body {
  margin: 0;
  padding: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji" ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#aboutSection{
  min-height: 93.2vh;
}
#avatar{
  height: 300px;
  width: 300px;
  padding: 10px;
}
#avatarWrapper{
  border-radius: 10px;
  padding-top: 5vh;
}

#aboutSection .fab, #aboutSection .fas{
  padding-right: 15px;
  padding-top: 10px;
  text-decoration: none;
  color: #f8f9fa;
  font-size: 45px;
}

#projectSection{
  background-color: white;
  width: 100%;
  padding-bottom: 5vh;
}
#projectSection .fas{
  font-size: 17px;
  padding: 0 3px;
}
.card-scroll-container{
  min-height: 77.5vh;
  max-height: 80vh;
  padding: 5px;
}
.badge-pill{
  padding: .3em .8em;
}
.badge-danger{
  background-color: rgb(125, 20, 34);
}
.badge-primary{
  background-color: rgb(6, 84, 148);
}
.card-body {
  min-width: 30vw;
  margin: 0 10px;
}
@media (min-width: 576px) and (max-width: 1199px) {

  .card-scroll-container{
    max-width: 100vw;
    overflow-x: scroll;
  }
  .card-body {
      min-height: 300px;
      min-width: 50vw;
      margin: 0 10px;
  }
}

@media (max-width: 575.98px) {
  #projectSection{
    padding-bottom: 1vh;
  }
  .card-scroll-container{
    overflow-x: scroll;
    max-height: 300px;
    max-width: 100%;
  }
  .card-body {
      min-height: 300px;
      min-width: 350px;
      margin-right: 15px;
  }
}
.Ruby{
  border-color: rgb(126, 45, 0);
  background-color: rgb(126, 45, 0);
  color: white;
}
.React{
  border-color: rgb(29, 136, 210);
  background-color: rgb(29, 136, 210);
  color: white;
}
.NodeJS{
  border-color: rgb(64, 131, 11);
  background-color: rgb(64, 131, 11);
  color: white;
}
.badge{
  margin-right: 5px;
}

.spacer{
  min-height: 25vh;
  height: 61vh;
  background: url(/portfolio/static/media/placeholder.f015198c.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 575.98px) {
  .spacer{
    height: 52vh;

  }

}

#contactSection{
  background-color: white;
  padding-top:35vh;
  padding: 25px;
  min-height: 25vh;
}
#contactSection .fa-envelope{
  font-size: 45px;
  padding-top: 15px;
}
.fa-envelope:hover{
  cursor: pointer;
  color: #007bff !important;
}

#footerSection{
  background-color: white;
  padding: 10px;
  min-height: 9vh;
}
@media (max-width: 575.98px) {
  #footerSection{
    height: 8vh;
    padding: 0px;
  }
  #footerSection .fab, #footerSection .fas{
    padding-top:5px;
    font-size: 40px;
  }
}
#footerSection .fab, #footerSection .fas{
  padding-right: 15px;
  text-decoration: none;
  color: #343a40;
  font-size: 45px;
}
#footerSection .fab:hover, #footerSection .fas:hover{
  color: #007bff !important;
}

body{
  background: url(/portfolio/static/media/preview.c5732171.jpg);
  background-attachment: fixed;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
}
.section a{
  color: #343a40;
}
.section a:hover{
  color: #007bff !important;
}
#bodyContainer{
  margin-top: 56px;
  padding-top: 10px;
}
.section{
  width: 99.1vw;
  margin: 0;
}
.btn-default{
  background-color: #343a40 !important;
  color: white;
}
.btn-default:hover{
  background-color: #007bff !important;
}

